$yellow: rgba(244, 214, 163, 0.3);
$dark-yellow: #f4d6a3;
$pink: rgba(249,200,194, 0.5);
$body-font: 'Poppins', sans-serif;
$header-font: 'Mont Heavy', sans-serif;
$accent-font: 'Sonder', serif;
$selection-color: rgba(158,222,219, 0.5);
$body-color: #3a3a3a;
$accent-color: #097a88;
$subtitle-color: #727078;
$background-color: #fffdf4;

$portola-body-font: 'portola-body', sans-serif;
$portola-header-font: 'portola-primary', sans-serif;
$portola-selection-color: rgba(158,222,219, 0.5);
$portola-blue: #002fa7;

html {
	cursor: url(https://media-prd.coachella.com/content/coachella-2022/cursor.png),auto;
	background: $background-color;
}

h1 {
	font-family: $accent-font;
	font-weight: 100;
	font-size: 5em;
	//margin: 0.1em 0;
	color: $body-color;
}

h2, h3, h4, {
	margin: 0.1em 0;
	font-family: $header-font;
}

p {
	margin: 0.1em 0;
	font-family: $body-font;
}

button {
	padding: 12.5px 18px;
	margin: 0 5px;
	font-family: $body-font;
	color: $body-color;
	border: 2px solid $body-color;
	background: transparent;
	font-weight: 800;
	cursor: pointer;
}

.background {
	display: none;
}

.content {
	width: 100%;
	height: 100vh;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 3;
	overflow-y: scroll;
}


.festival-options-wrapper {
	width: 100%;
	height: 100vh;
	background: #fffdf4;
	display: flex;
	align-content: center;
	justify-content: center;
}

.festival-options-wrapper {
	width: 600px;
	margin: 0 auto;
	flex-wrap: wrap;
}

.festival-option {
	//border: 2px solid black;
	position: relative;
	width: 600px;
	margin: 10px 0;
	text-align: center;
	background-size: cover;
	background-position: center center;
	padding: 100px 0;
	cursor: pointer;

	&.coachella {
		background-color: #f9c012;
	}

	&.portola {
		background-color: $portola-blue;
		h2 {color:white;}
	}

	&.osl {
		background-color: #6b6e29;
		h2 {color:white;}
	}
}

.festival-option-bg {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	background-size: cover;
	background-position: center center;
	transition: 0.5s;

	&:hover {
		opacity: 0;
	}
}

.coachella .festival-option-bg {
	background-image: url('./images/coachella.jpeg');
}

.portola .festival-option-bg {
	background-image: url('./images/portola.jpg');
}

.osl .festival-option-bg {
	background-image: url('./images/outsidelands.jpeg');
}

.day-header {
	margin: 50px 0;
	text-align: center;

	img {
		max-width: 600px;
	}
}

.nav-wrapper {
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 30px 0 50px;

	h4 {
		margin: 0 20px;
		cursor: pointer;
		position: relative;

		&:before {
			content: "";
	    position: absolute;
	    width: 100%;
	    min-width: 85px;
	    height: 10px;
	    opacity: 0;
	    bottom: -5px;
	    left: 50%;
	    -webkit-transform: translateX(-50%);
	    transform: translateX(-50%);
	    background-repeat: no-repeat;
	    background-size: 100% 100%;
	    z-index: -1;
	    -webkit-clip-path: polygon(0 0,100% 0,0 50%,100% 100%,0 100%);
	    clip-path: polygon(0 0,100% 0,0 50%,100% 100%,0 100%);
	    -webkit-transition: all .5s ease;
	    transition: all .5s ease;
		}

		&:hover:before, &.selected:before  {
	    opacity: 1;
	    -webkit-clip-path: polygon(0 0,100% 0,100% 50%,100% 100%,0 100%);
	    clip-path: polygon(0 0,100% 0,100% 50%,100% 100%,0 100%);
		}
	}

	#lineup:before {
	  background-image: url('./images/nav-underline-lineup.png');
	}

	#schedule:before {
		background-image: url('./images/nav-underline-schedule.png');
	}
}

.day-wrapper {
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 0 0 50px;

	button.selected {
		background: $body-color;
		color: white;
	}
}

.lineup-wrapper {
	margin: 20px auto;
	width: 100%;
	max-width: 1200px;
	overflow-x: scroll;

	.lineup {
		display: flex;
	}

	.stage-wrapper {
		margin: 0 10px;
		width: 200px;
		flex-grow: 0;
		flex-shrink: 0;
	}

	.artist-wrapper {
		border: 1px solid black;
		padding: 10px;
		margin: 10px 0;
		cursor: pointer;

		p {
			font-size: 12px;
		}

		&.selected {
			background: $selection-color;
		}
	}
}

.schedule-wrapper {
	width: 100%;
	max-width: 800px;
	margin: 20px auto;
	padding: 20px;
	box-sizing: border-box;

	.schedule-day {
		margin: 20px 0;
		padding: 20px 0;
	}

	.schedule-item-wrapper {
		border: 1px solid black;
		padding: 10px;
		margin: 10px 0;

		.schedule-item-location {
			display: flex;
			align-items:center;
			color: $body-color;
			

			h4 {
				font-family: 'Mont Bold';
				font-weight: 200;
			}
		}

		img {
			width: 12px;
			height: 16px;
			margin-right: 5px;
		}
	}
}

.conflict-wrapper {
	//border: 1px solid red;
	background: $pink;
	padding: 20px;
	margin: 10px 0;
	position: relative;

	.conflict-acts-wrapper {
		display: flex;
		align-items: center;

		.act-wrapper {
			width: 200px;

			&:first-of-type {
				margin-left: 0;
			}
		}
	}

	button {
		position: absolute;
		right: 20px;
		bottom: 30px;
	}
}

.gap-wrapper {
	//border: 1px solid yellow;
	background: $yellow;
	padding: 20px;
	margin: 10px 0;
	position: relative;

	.gap-artists-wrapper {
		display: flex;
		flex-wrap: wrap;
		height: 110px;
		overflow: hidden;
		margin-bottom: 10px;

		&.open {
			height: auto !important;
		}
	}

	a {
		cursor: pointer;
		text-decoration: underline;
	}

	.act-wrapper {
		width: 200px;
	}

	#close {
		position: absolute;
		top: 20px;
		right: 20px;
		cursor: pointer;
	}

	&.hidden {
		display: none;
	}
}

.act-wrapper {
	border: 1px solid black;
	padding: 10px;
	margin: 10px 5px;
	position: relative;

	.act-location {
		display: flex;
		align-items:center;
		color: $body-color;
		
		h4 {
			font-family: 'Mont Bold';
			font-weight: 200;
		}

		img {
			width: 12px;
			height: 16px;
			margin-right: 5px;
		}
	}

	.action {
		position: absolute;
		right: 10px;
		bottom: 10px;
		text-decoration: underline;
		font-size: 12px;
	}
}

.footer {
	display: none;
}

@media only screen and (max-width: 650px) {
	.conflict-wrapper button {
		position: relative;
		right: auto;
		bottom: auto;
		margin-left: 0;
	}

	#bg2 {
		display: none;
	}

	.act-wrapper {
		padding-bottom: 20px;
	}
}

.coachella {
	.background {
		width: 100%;
		height: 100vh;
		position: absolute;
		z-index: 1;
		top: 0;
		left: 0;
		overflow: hidden;
		display: block;
	
		img {
			position: absolute;
		}
	
		#bg1 {
			top: 0;
			left: 0;
			z-index: 2;
		}
	
		#bg2 {
			right: 0;
			top: 0;
		}
	}
}

.portola {
	.content {
		background: #f0f0f0;
	}

	h1, h2, h3, h4 {
		color: $portola-blue;
		font-family: $portola-header-font;
	}

	h2, h4 {
		text-transform: uppercase;
	}

	p {
		font-family: $portola-body-font;
	}

	button {
		font-family: $portola-body-font;
		border: 2px solid #000000;
		padding: 5px 10px;
		font-size: 20px;
		color: #000;

		&.selected {
			background-color: #000;
		}
	}

	.lineup-wrapper .artist-wrapper.selected {
		background: $portola-blue;
		border-color: $portola-blue;
		h3, p {color: white;}
	}

	.footer {
		width: 100%;
		display: block;

		img {
			width: 100%;
		}
	}
}

@font-face {
	font-family: 'Gopher Medium';
	src: url('./fonts/Gopher-Medium.ttf');
	font-weight: normal;
	font-style: normal;
  }

  @font-face {
	font-family: 'Gopher Bold';
	src: url('./fonts/Gopher-Bold.ttf');
	font-weight: bold;
	font-style: normal;
  }

  @font-face {
	font-family: 'Tablet Gothic';
	src: url('./fonts/Tablet Gothic W02 Regular.ttf');
	font-weight: normal;
	font-style: normal;
  }

$osl-teal: #102b31;
$osl-bg: #FFF2CC;
$osl-selected: #ffcc33;

$stage-colors: (
	"sutro": #FDAED8,
	"panhandle": #849A4B,
	"lands_end": #EB483E,
	"cocktail_magic": #FCC425,
	"soma_tent": #00507D,
	"twin_peaks": #FE9C27,
	"dolores": #B057C8,
	"the_music_den_by_toyota": $osl-teal
);

.osl {
	.content {
		background: $osl-bg;
	}

	h2, h3, h4 {
		color: $osl-teal;
	}

	h1, h2 {
		font-family: Gopher Bold;
	}

	h3, h4 {
		font-family: Gopher Medium;
	}

	p {
		font-family: Tablet Gothic;
	}

	button {
		font-family: Tablet Gothic;
		border-color: $osl-teal;
		color: $osl-teal;
		text-transform: uppercase;
		border-radius: 15px;

		&.selected {
			background: $osl-teal;
		}
	}

	.lineup-wrapper .artist-wrapper {
		border: none;

		h3, p {
			color: white;
		}

		&.selected {
			background-color: $osl-selected !important;
			outline: 2px solid $osl-selected;
			border: 1px solid $osl-teal;

			h3, p {
				color: $osl-teal;
			}
		}
	}

	.schedule-wrapper .schedule-item-wrapper, .act-wrapper {
		border: none;

		h3, h4, p {
			color: white;
		}

		h4 {
			font-family: Tablet Gothic;
		}
	}

	.conflict-wrapper {
		background: transparent;
		border: 2px solid #EB483E;

		&> h3 {color: #EB483E;}
	}

	@each $key,$val in $stage-colors{
		.#{$key} .artist-wrapper {
		  background-color: #{$val};
		}

		.schedule-wrapper .schedule-item-wrapper.#{$key}, .act-wrapper.#{$key} {
			background-color: #{$val};
		  }
	  }	  
}